
import { defineComponent, ref, onMounted, SetupContext } from "vue";
import { IMessageTemplate, IProduct } from '@/types/Interfaces';

export default defineComponent({
  components: {
    
  },
  props: {
    product: {
      type: Object as () => IProduct
    }
  },
  emits: [
    'close',
    'select:template'
  ],
  setup(_, context: SetupContext) {
    
    const onClose = () => {
      context.emit('close')  
    };
    return {
      onClose,
    }
  }
})
