        copy.push(cloneDeep(p))
        context.emit('update:cart', copy)
      } else {
        const index = props.cart.findIndex((cp: IProduct) => cp.id === p.id);
        if (index >= 0) {
          // remove
          copy.splice(index, 1)
          
          context.emit('update:cart', copy)
        }
      }
    };

